$body-text-family: 'MMTextProWeb-regular' !default;
$body-text-family-bold: 'MMTextProWeb-bold' !default;
$headline-text-family: 'MMHeadlineProWeb-regular' !default;
$icon-font: 'mm-icons' !default;
$line-height: 1.25 !default;
$font-size-p: 16px !default;
$line-height-p: 1.25 !default;

.rbkit-container {
    width: 100% !important;
    .hide-on-mobile{
        display: none !important;
    }
    .full-width {
        width: 100%;
    }
    h1, .h1 {
        font-size: 10vw !important;
    }
    .price {
        font-family: MMHeadlineProWeb-Regular;
        font-size: 36px;
        line-height: .8;
        text-shadow: -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff, 1px 1px 0 #fff, 0 2px 7px rgba(0, 0, 0, .5);
        color: #000 !important;
    }
    .button {
        height: 40px;
        box-sizing: border-box;
    }

    .rbkit-product-slider {
        p.slider-vat-label {
            font-size: 11px !important;
        }
        .swiper-slide .product-teaser-container figure {
            height: 100px;
        }
        .swiper-container {
            background: none !important;
        }
        h2, p {
            margin: 0;
        }
        h2{
            padding-left: 5px;
        }
    }

    .slick-dots {
        margin: 0;
        padding: 0;
    }

    .section-abbinder {
        box-sizing: border-box;
        padding: 0 10px;
        h2 {
            font-size: 24px;
            color: #444;
            padding-left: 10px;
            margin: 0;
            margin-bottom: 5px;
            width: 100%;
            line-height: 1;
        }

        div {
            box-sizing: border-box;
            border: 1px solid #ddd;
            background: #fff;
            padding: 20px 20px 10px 10px;
            margin-bottom: 12px;
            width: 100%;

            h3 {
                font-size: 20px;
                padding-left: 10px;
                line-height: 1;
                margin-bottom: 16px;
                font-family: mm-headline, MMHeadlineProWeb-regular !important;
                color: #df0000;
            }
            p {
                padding-left: 10px;
                font-size: 14px;
                color: #444;
            }
            ul {
                padding: 0;
                margin: 0;
                li {
                    display: inline-block;
                    border-right: 1px solid #444;
                    padding: 0 10px;
                    margin-bottom: 10px;
                    &:last-child {
                        border: none;
                    }
                    a {
                        color: #444;
                        font-size: 13px;
                        &:hover {
                            color: #df0000;
                        }
                    }

                }
            }
            .newsletter-form {
                padding-top: 2px;
                padding-bottom: 10px;
            }

            fieldset {
                width: 230px;
                position: relative;
                margin-left: 10px;
                label {
                    height: 0;
                    margin: 0;
                    overflow: hidden;
                    position: absolute;
                    text-indent: -9999px;
                }
                input {
                    width: 172px;
                    background: none repeat scroll 0 0 #F2F2F2;
                    border: 1px solid #ddd;
                    border-bottom-left-radius: 4px;
                    -webkit-border-bottom-left-radius: 4px;
                    -moz-border-radius-bottomleft: 4px;
                    border-top-left-radius: 4px;
                    -webkit-border-top-left-radius: 4px;
                    -moz-border-radius-topleft: 4px;
                    -webkit-box-shadow: inset 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
                    -moz-box-shadow: inset 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
                    box-shadow: inset 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
                    color: #000;
                    float: left;
                    height: 38px;
                    line-height: normal;
                    margin: -2px 0 0;
                    outline: medium none;
                    padding: 0 10px;
                    width: 195px;
                }

                button {
                    -webkit-border-radius: 0 4px 4px 0 !important;
                    -moz-border-radius: 0 4px 4px 0 !important;
                    -ms-border-radius: 0 4px 4px 0 !important;
                    border-radius: 0 4px 4px 0 !important;
                    background: #fff;
                    border: 1px solid #ddd;
                    height: 38px !important;
                    overflow: hidden;
                    text-indent: -9999px;
                    top: -2px;
                    width: 34px;
                    box-sizing: border-box;
                    &:before {
                        color: #000;
                        font-size: 15px;
                        height: 17px;
                        left: 13px;
                        overflow: hidden;
                        padding-top: 1px;
                        position: absolute;
                        text-indent: 0;
                        top: 10px;
                        width: 9px;
                        font-family: 'mm-icons';
                        content: "\e603";
                    }

                }
            }

        }
    }


    .rb-product-info {
        .rating {
            display: block;
            margin-bottom: 10px;
        }
        .product-image-container {
            position: relative;
            .pdp-product-info {
                margin-top: 0;
                position: absolute;
                top: 20px;
                left: 0;
            }
        }
        .brand-img {
            display: block;
            margin-bottom: 10px;
        }
        .price-container {
            display: flex;
            flex-wrap: wrap;
            .price {
                width: auto !important;
                font-size: 42px;
            }
            .price-old {
                margin-bottom: 0 !important;
                font-size: 28px;
                margin-left: 10px;
                position: relative;
                &:after{
                    content:"";
                    border-top:2px solid #df0000;
                    position: absolute;
                    left:0;
                    right: 0;
                    top:10px;
                }
            }
            .price-old-info {
                position: absolute;
                right: -20px;
                top: 0;
                i {
                    font-size: 12px;
                    color: #000;
                    vertical-align: top;
                    line-height: 1;
                    font-family: mm-text-bold, MMTextProWeb-Bold;
                    text-shadow:none;
                }
            }
            .vat {
                margin: 5px 0 5px;
                font-size: 12px;
                width: 100%;
            }
        }
        .product-image {
            margin-bottom: 10px;
        }
        .product-features {
            margin-bottom: 10px;
            p {
                display: flex;
                margin-bottom: 0;
                &.article-number {
                    margin-bottom: 12px;
                }
                .feature-name {
                    width: 200px;
                    display: inline-block;
                }
                .feature-value {
                    width: 360px;
                    display: inline-block;
                }
            }
        }
        .button {
            margin-bottom: 10px;
        }
    }


}

//hide product details in pds specials
#accordion-highlight-details {
    display: none;
}