
.rbkit-container {

    .rbkit-table {
        margin: -2px;

        .table-select {
            position: relative;
            height: 44px;
            overflow: visible;
            display: flex;
            z-index: 1;
            > div {
                position: relative;
                width: 50%;
                display: none;

                > div {
                    position: absolute;
                    width: 100%;
                    display: flex;
                    flex-flow: column wrap;
                    overflow: hidden;
                    top: 0;
                    border: 2px solid #fff;
                    background: #eee;
                    box-sizing: border-box;
                    height: 44px;

                    &.open {
                        height: auto;
                        .open-select {
                            transform: rotate(180deg);
                            text-align: left;
                        }
                    }

                    .open-select {
                        position: absolute;
                        right: 0;
                        top: 0;
                        padding: 9px;
                        box-sizing: border-box;
                        cursor: pointer;
                        width: 100%;
                        text-align: right;
                        &:before {
                            content: "\e604";
                            font-family: mm-icons;
                            color: #000;
                            font-size: 12px;

                        }

                    }
                    a {
                        display: flex;
                        padding: 5px;
                        order: 1;
                        height: 40px;
                        line-height: 1;
                        box-sizing: border-box;
                        color: #000;
                        cursor: pointer;
                        align-items: center;
                        font-size: 16px;
                        //word-break: break-word;
                        width: 100%;
                        &.active {
                            font-family: MMTextProWeb-Bold, mm-text-bold;
                            pointer-events: none;
                            order: 0;
                            text-decoration: none;
                            padding-right: 40px;
                            //word-break: break-word;
                        }
                        &.disabled {
                            opacity: .5;
                            pointer-events: none;
                        }
                    }
                    p {
                        padding: 10px 5px;
                        line-height: 20px;
                        color: #000;
                        text-align: center;
                    }
                }


            }
        }
        .table-container {
            display: flex;
            flex-flow: row wrap;
            &[data-row="0"] {
                display: none;
                font-family: MMTextProWeb-Bold, mm-text-bold;
            }
            > div {
                width: 50%;
                text-align: center;
                justify-content: center;
                align-items: center;
                background: #eee;
                border: 2px solid #fff;
                box-sizing: border-box;
                order: 3;
                padding: 10px 5px;
                display: none;
                min-height: 40px;
                &[data-col="0"] {
                    display: flex;
                    order: 0;
                    &.table-empty {
                        display: none;
                    }
                }
                &[data-order="1"] {
                    order: 1;
                    display: flex;
                }
                &[data-order="2"] {
                    order: 2;
                    display: flex;
                }
                &.table-img {
                    > img {
                        max-width: 100%;
                        width: 100%;
                    }
                }

                &.table-button {

                    > a {
                        width: auto;
                        max-width: 100%;
                        padding-left: 10px;
                        padding-right: 10px;
                    }
                }

                .check {
                    &:before {
                        content: "\e634";
                        font-family: mm-icons;
                        color: #57b119;
                        font-size: 20px;
                    }
                }
                .minus {
                    &:before {
                        content: "\e635";
                        font-family: mm-icons;
                        color: #e00505;
                        font-size: 20px;
                    }
                }
                > span {
                    //word-break: break-word;
                }

                .table-dyn-info{
                    position: relative;
                    text-align: center;
                    .price{
                        display: inline-block !important;
                        width: auto !important;
                        margin: 5px 0 0 !important;
                        span{
                            font-size: 24px;
                        }
                    }
                    img{
                        max-width: 100%;
                    }
                }

            }
        }

        &.col5, &.col4, &.col3 {
            span {
                font-size: 16px;
            }
            .table-select {
                > div {
                    width: auto;
                    flex: 1;
                    &[data-select="0"] {
                        display: block;
                    }

                }
            }
            .table-container {
                > div {
                    &[data-order="3"] {
                        order: 3;
                        display: flex;
                    }
                    width: auto;
                    flex: 1;
                    &[data-col="0"] {
                        width: auto;
                        &.table-empty {
                            display: flex;
                        }
                    }

                }
            }

        }
        &.col5, &.col4 {
            .table-container {
                > div {
                    &[data-order="4"] {
                        order: 4;
                        display: flex;
                    }

                }
            }

        }
        &.col5 {
            .table-container {
                > div {
                    &[data-order="5"] {
                        order: 5;
                        display: flex;
                    }

                }
            }
        }

        &.col3top, &.col2top{
            span {
                font-size: 16px;
            }
            .table-select {
                height: 88px;
                flex-flow: row wrap;
                > div {
                    &[data-select="0"] {
                        width: 100%;

                    }
                }
            }
            .table-container {
                > div {

                    &[data-col="0"] {
                        width: 100%;

                    }

                }

            }
        }


        &.col3top {

            .table-select {

                > div {
                    width: 33.33%;

                }
            }
            .table-container {
                > div {
                    width: 33.33%;

                    &[data-order="3"] {
                        order: 3;
                        display: flex;
                    }

                }

            }

        }

    }

}


